.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__star:last-child {
    margin-right: 0;
  }

  @each $name, $index in $indexes {
    &--#{$name} &__star:nth-child(-n + #{$index}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
